@import url(https://fonts.googleapis.com/css2?family=Merienda&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.homePage {
  font-size: 20px;
  font-family: "Merienda", cursive;
  box-sizing: border-box;
  max-width: 100vw;
  min-height: 100vh;
  padding: 15% 0 10% 0;
  background: linear-gradient(
    225deg,
    rgba(255, 208, 228, 1) 9%,
    rgba(246, 173, 203, 1) 20%,
    rgba(191, 133, 158, 1) 38%,
    rgba(135, 69, 98, 1) 80%
  );
}
.intro {
  box-sizing: border-box;
  width: 90%;
  max-width: 95vw;
  height: 600px;
  margin: 10vh auto auto auto;
  padding: .5em;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  overflow: hidden;
}
.hello {
  font-size: 3em;
  color: rgba(235, 199, 199, 0.486);
}
.about {
  font-size: 1em;
  color: rgb(235, 199, 199);
  text-shadow: 2px 2px 3px rgba(255, 255, 255, 0.2);
}
.light {
  opacity: 0.5;
  -webkit-animation: flash 1.5s ease-out alternate infinite;
          animation: flash 1.5s ease-out alternate infinite;
}
.lighter {
  opacity: 0;
  -webkit-animation: brighter 1.5s ease-out alternate infinite;
          animation: brighter 1.5s ease-out alternate infinite;
}
span:not(.light):not(.lighter) {
  opacity: 0;
  -webkit-animation: lights 1.5s ease-out alternate infinite;
          animation: lights 1.5s ease-out alternate infinite;
}

@-webkit-keyframes lights {
  to {
    opacity: 0.2;
  }
}

@keyframes lights {
  to {
    opacity: 0.2;
  }
}
@-webkit-keyframes brighter {
  to {
    opacity: 0.5;
  }
}
@keyframes brighter {
  to {
    opacity: 0.5;
  }
}
@-webkit-keyframes flash {
  to {
    opacity: 1;
    text-shadow: 0 0 80px white, 0 0 90px rgba(255, 255, 255, 0.788);
  }
}
@keyframes flash {
  to {
    opacity: 1;
    text-shadow: 0 0 80px white, 0 0 90px rgba(255, 255, 255, 0.788);
  }
}

@media (min-width: 992px) {
    .intro {
      width: 70%;
      display: flex;
      -webkit-columns: 20% 60%;
              columns: 20% 60%;
      justify-content: space-between;
      align-items: center;
      padding: 2em;
      margin: auto;
    }
    .hello {
      flex-basis: 15%;
      font-size: 8em;
    }
    .about {
      flex-basis: 65%;
      font-size: 1.4em;
    }
  }
  
.navbarOuter {
  position: fixed;
  width: 100vw;
  right: 0;
  margin: auto;
  background: linear-gradient(
    262deg,
    rgb(255, 211, 229) 2%,
    rgb(255, 192, 220) 18%,
    rgba(191, 133, 158, 1) 47%,
    rgba(150, 91, 117, 1) 83%
  );
  z-index: 1;
}
button {
  color: rgb(231, 175, 224);
  font-size: 25px;
  width: 45px;
  height: 45px;
  padding-top: 5px;
  border-radius: 30px;
  background: linear-gradient(225deg, #ffeafa, #e6c5d3);
  box-shadow: 0px 3px 8px #aaa, inset 0px 2px 3px #fff;
  border: none;
  outline: none;
  margin: 10px;
  float: right;
  display: none;
}
.hamburger {
  border-radius: 10px;
  display: block;
}
button:active {
  box-shadow: inset 0px -2px 5px rgb(255, 255, 255),
    inset 0px 2px 5px rgba(0, 0, 0, 0.15);
  color: rgb(170, 105, 157);
}

@media (min-width: 992px) {
  button {
    display: block;
    width: 60px;
    height: 60px;
  }
  .hamburger {
    display: none;
  }
}

.workOuter {
    position: relative;
  background: linear-gradient(
    90deg,
    rgb(204, 147, 172) 40%,
    rgb(255, 255, 255) 40%
  );
  min-height: 100vh;
  max-width: 100vw;
}
.cubesOuter {
    height: 100vh;
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    margin: auto;
}
.cubes {
  font-size: 15em;
  color: rgba(135, 69, 98, 0.1);
}

@media (min-width: 992px) {
  .cubes {
    font-size: 50em;
  }
}

.App {
  text-align: center;
}
::-webkit-scrollbar {
  width: 15px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 20px pink; 
}
::-webkit-scrollbar-thumb {
  background: pink; 
  border-radius: 50px;
}
::-webkit-scrollbar-thumb:hover {
  background: rgb(245, 145, 162); 
}

